.work-xp{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px 16px;
}
.circle{
    width: 12px;
    height: 12px;
    min-width: 12px;
    min-height: 12px;
    max-width: 12px;
    max-height: 12px;
    border-style: solid;
    border-radius: 12px;
    border-width: 3px;
    border-color: #F2188F;
}
.circle.active{
    border-color: #A2AD91;
}
.work-data{
    display: flex;
    flex-direction: column;
    padding: 8px 16px;
    justify-content: center;
}
.work-title-year{
    display: flex;
    flex-direction: row;
    padding: 4px 0px;
    /* justify-content: center; */
    align-items: center;
}
.work-title{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 18px;
}
.work-sep{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 18px;
    padding: 0px 4px;
}
.work-duration{
    font-weight: 600;
    color: #F2188F;
    font-size: 16px;
}
.work-tech{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 15px;
}
.work-duration.active{
    color: #A2AD91;
}
.work-description{
    font-weight: 400;
    color: #eee;
    font-size: 16px;
    padding: 4px 0px;
    justify-content: center;
}
.work-techs{
    font-weight: 400;
    color: #eee;
    font-size: 16px;
    padding: 4px 8px;
    justify-content: center;
}

@media only screen and (max-width: 600px) {
    .work-xp{
        padding: 8px 8px;
    }
}