.edu-xp{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px 16px;
}
.line{
    width: 12px;
    height: 12px;
    min-width: 12px;
    min-height: 12px;
    max-width: 12px;
    max-height: 12px;
    border-radius: 2px;
    margin: 0px 6px;
    background-color: #F2188F;
}
.line.active{
    background-color: #A2AD91;
}
.edu-data{
    display: flex;
    flex-direction: column;
    padding: 8px 16px;
    justify-content: center;
}
.edu-title-year{
    display: flex;
    flex-direction: row;
    padding: 0px 0px;
    /* justify-content: center; */
    align-items: center;
}
.edu-title{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 18px;
}
.edu-sep{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 18px;
    padding: 0px 4px;
}
.edu-duration{
    font-weight: 600;
    color: #F2188F;
    font-size: 16px;
}
.edu-duration.active{
    color: #A2AD91;
}
.edu-where{
    font-weight: 400;
    color: #eee;
    font-size: 16px;
    padding: 4px 0px;
    justify-content: center;
}
.edu-gpa-title{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 15px;
}
.edu-gpa{
    font-weight: 400;
    color: #eee;
    font-size: 16px;
    padding: 4px 8px;
    justify-content: center;
}
@media only screen and (max-width: 600px) {
    .edu-xp{
        padding: 8px 8px;
    }
}