.info-section{
    display: flex;
    flex-direction: column;
    padding: 0px 16px;
}   
.section-header{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.section-icon{
    height: 42px;
    width: 42px;
    margin: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    background-color: blue;
}
.section-icon svg{
    fill: #fff;
    height: 24px;
    width: 24px;
}
.section-title{
    font-family: GoogleSans;
    font-weight: 600;
    color:#fff;
    padding:0px 10px;
}
.section-content{
    display: flex;
    flex-direction: column;
}
