.pane{
    display: flex;
    flex-direction: column;
    padding: 16px 8px;
    margin: 16px 32px;
    background-color: #121317;
    border-radius: 12px;
}   
.pane .section-icon{
    background-color: #A2AD91;
}

@media only screen and (max-width: 600px) {
    .pane{
        padding: 16px 8px;
        margin: 16px 12px;
    }   
}