@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
*{
  font-family: Raleway, sans-serif;
  -moz-user-select: none;
  -webkit-user-drag: none;
  -webkit-user-select: none;
}
body {
  background-color: #1b1e24;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
	font-family: GoogleSans;
	font-style: normal;
	font-weight: bold;
	src: url(/static/media/GoogleSans-Bold.4457817a.ttf) format('truetype');
}
@font-face {
	font-family: GoogleSans;
	font-style: normal;
	font-weight: 500;
	src: url(/static/media/GoogleSans-Medium.8d57e401.ttf) format('truetype');
}
@font-face {
	font-family: GoogleSans;
	font-style: normal;
	font-weight: 300;
	src: url(/static/media/GoogleSans-Regular.b5c77a6a.ttf) format('truetype');
}
.resume-container{
    display: flex;
    flex-direction: row;
    width: 100vw;
    min-height: 100vh;
}
.resume-info{
    display: flex;
    flex-direction: column;
    flex: 6 1;
    background-color: #24272e;
}
.resume-details{
    display: flex;
    flex-direction: column;
    flex: 15 1;
    background-color: #1b1e24;
}
.resume-profile{
    font-weight: 400;
    color: gainsboro;
    padding: 16px 32px;
    text-align: justify;
    font-size: 15px;
    line-height: 1.5;
}

@media only screen and (max-width: 600px) {
    .resume-container{
        flex-direction: column;
    }
}
.profile-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 0px;
}
.profile-pic{
    width: 96px;
    height: 96px;
    -webkit-clip-path: circle(46px at center);
            clip-path: circle(46px at center);
}
.profile-name{
    font-size: 24px;
    color: #fff;
    padding: 16px 0px;
}
.profile-first{
    font-family: Raleway;
    font-weight: 800;
    padding: 0px 3px;
}
.profile-last{
    font-family: Raleway;
    font-weight: 300;
    font-style: italic;
    padding: 0px 3px;

}
.profile-position{
    color: #ccc;
    font-style: italic;
}
.info-section{
    display: flex;
    flex-direction: column;
    padding: 0px 16px;
}   
.section-header{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.section-icon{
    height: 42px;
    width: 42px;
    margin: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 12px;
    background-color: blue;
}
.section-icon svg{
    fill: #fff;
    height: 24px;
    width: 24px;
}
.section-title{
    font-family: GoogleSans;
    font-weight: 600;
    color:#fff;
    padding:0px 10px;
}
.section-content{
    display: flex;
    flex-direction: column;
}

.info-panel{
    display: flex;
    flex-direction: column;
    padding: 8px 16px;
}   
.info-item{
    display: flex;
    cursor: pointer;
    flex-direction: row;
    padding: 8px 16px;
    align-items: center;
    justify-content: space-between;
}
.info-title{
    font-weight: 800;
    color:#fff;
}
.info-value{
    font-weight: 500;
    color:#ddd;
    font-family: GoogleSans;
}
.pane{
    display: flex;
    flex-direction: column;
    padding: 16px 8px;
    margin: 16px 32px;
    background-color: #121317;
    border-radius: 12px;
}   
.pane .section-icon{
    background-color: #A2AD91;
}

@media only screen and (max-width: 600px) {
    .pane{
        padding: 16px 8px;
        margin: 16px 12px;
    }   
}
.work-xp{
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 8px 16px;
}
.circle{
    width: 12px;
    height: 12px;
    min-width: 12px;
    min-height: 12px;
    max-width: 12px;
    max-height: 12px;
    border-style: solid;
    border-radius: 12px;
    border-width: 3px;
    border-color: #F2188F;
}
.circle.active{
    border-color: #A2AD91;
}
.work-data{
    display: flex;
    flex-direction: column;
    padding: 8px 16px;
    justify-content: center;
}
.work-title-year{
    display: flex;
    flex-direction: row;
    padding: 4px 0px;
    /* justify-content: center; */
    align-items: center;
}
.work-title{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 18px;
}
.work-sep{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 18px;
    padding: 0px 4px;
}
.work-duration{
    font-weight: 600;
    color: #F2188F;
    font-size: 16px;
}
.work-tech{
    font-weight: 700;
    color: #FDD8EC;
    font-size: 15px;
}
.work-duration.active{
    color: #A2AD91;
}
.work-description{
    font-weight: 400;
    color: #eee;
    font-size: 16px;
    padding: 4px 0px;
    justify-content: center;
}
.work-techs{
    font-weight: 400;
    color: #eee;
    font-size: 16px;
    padding: 4px 8px;
    justify-content: center;
}

@media only screen and (max-width: 600px) {
    .work-xp{
        padding: 8px 8px;
    }
}
