.info-panel{
    display: flex;
    flex-direction: column;
    padding: 8px 16px;
}   
.info-item{
    display: flex;
    cursor: pointer;
    flex-direction: row;
    padding: 8px 16px;
    align-items: center;
    justify-content: space-between;
}
.info-title{
    font-weight: 800;
    color:#fff;
}
.info-value{
    font-weight: 500;
    color:#ddd;
    font-family: GoogleSans;
}