.resume-container{
    display: flex;
    flex-direction: row;
    width: 100vw;
    min-height: 100vh;
}
.resume-info{
    display: flex;
    flex-direction: column;
    flex: 6;
    background-color: #24272e;
}
.resume-details{
    display: flex;
    flex-direction: column;
    flex: 15;
    background-color: #1b1e24;
}
.resume-profile{
    font-weight: 400;
    color: gainsboro;
    padding: 16px 32px;
    text-align: justify;
    font-size: 15px;
    line-height: 1.5;
}

@media only screen and (max-width: 600px) {
    .resume-container{
        flex-direction: column;
    }
}