.profile-info{
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 30px 0px;
}
.profile-pic{
    width: 96px;
    height: 96px;
    clip-path: circle(46px at center);
}
.profile-name{
    font-size: 24px;
    color: #fff;
    padding: 16px 0px;
}
.profile-first{
    font-family: Raleway;
    font-weight: 800;
    padding: 0px 3px;
}
.profile-last{
    font-family: Raleway;
    font-weight: 300;
    font-style: italic;
    padding: 0px 3px;

}
.profile-position{
    color: #ccc;
    font-style: italic;
}